<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <el-form-item label="" prop="factoryId">
            <!-- 输入工厂名称 start -->
            <!-- <el-input class="input-name" v-model="searchValidateForm.factory" placeholder="请输入工厂名称"></el-input> -->
            <el-select
              v-model="searchValidateForm.factory"
              filterable
              placeholder="请选择工厂名称"
              style="width: 100%;"
              clearable
            >
              <el-option
                v-for="(item, index) in factoryList"
                :key="index"
                :label="item.scenicSpotFname"
                :value="item.scenicSpotFid"
              >
              </el-option>
            </el-select>
            <!-- 输入工厂名称 end -->
          </el-form-item>

          <el-form-item label="" prop="name">
            <!-- 输入生产批次 start -->
            <el-input
              class="input-name"
              v-model="searchValidateForm.name"
              placeholder="请输入生产批次"
            ></el-input>
            <!-- 输入生产批次 end -->
          </el-form-item>
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->
        <!-- 搜索 end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="loadingTable"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 生产批次 start -->
          <el-form-item label="生产批次" prop="name">
            <el-input
              type="text"
              v-model="validateForm.name"
              autocomplete="off"
              placeholder="请输入生产批次"
            ></el-input>
          </el-form-item>
          <!-- 生产批次 end -->

          <!-- 工厂质检项 start -->
          <el-form-item label="工厂质检项" prop="inspectionId">
            <!-- <el-input type="text" v-model="validateForm.inspectionName" autocomplete="off" placeholder="请输入质检项"></el-input> -->
            <el-select
              v-model="validateForm.inspectionId"
              filterable
              placeholder="请选择工厂质检项"
              @change="onChangeControl"
              style="width: 100%;"
            >
              <el-option
                v-for="(item, index) in controlList"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 工厂质检项 end -->

          <!-- 景区验收项 start -->
          <el-form-item label="景区验收项" prop="appectanceId">
            <!-- <el-input type="text" v-model="validateForm.appectanceId" autocomplete="off" placeholder="请输入验收项"></el-input> -->
            <el-select
              v-model="validateForm.appectanceId"
              filterable
              placeholder="请选择景区验收项"
              style="width: 100%;"
              @change="onChangeAppectance"
            >
              <el-option
                v-for="(item, index) in checkList"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 景区验收项 end -->

          <!-- 机器人列表 start -->
          <el-form-item label="机器人信息" prop="robotTable">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="fs-18">机器人信息</span>
                <!-- 新增 start -->
                <el-tooltip
                  class="add-item"
                  effect="dark"
                  content="新增"
                  placement="top"
                  v-if="dialogState !== 'preview'"
                >
                  <el-button
                    class="icon-size"
                    style="float: right; padding: 3px 0"
                    type="text"
                    icon="el-icon-circle-plus-outline"
                    @click="onAddOperatorTable"
                  ></el-button>
                </el-tooltip>
                <!-- 新增 end -->
              </div>
              <!-- 表格 start -->
              <el-table
                class="robot-detail-table"
                :data="validateForm.robotList"
                style="width: 100%"
                border
                v-loading="tableLoading"
              >
                <!-- 展开行 start -->
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <template>
                      <el-form label-position="left" class="demo-table-expand">
                        <el-form-item
                          class="table-expand-form-item"
                          v-for="(value, key) in robotTableFields"
                          :key="key"
                          :label="key"
                        >
                          <template v-if="value == 'applicableModel'">
                            <!-- 机器人型号 start -->
                            <el-input
                              type="text"
                              v-model="scope.row[value]"
                              autocomplete="off"
                              placeholder="机器型号"
                            ></el-input>
                            <!-- 机器人型号 end -->
                          </template>

                          <template v-if="value == 'robotCount'">
                            <!-- 机器人数量 start -->
                            <el-input
                              ref="robotTableInputRef"
                              class="input-name"
                              v-model="scope.row[value]"
                              placeholder="机器人数量"
                            ></el-input>
                            <!-- 机器人数量 end -->
                          </template>
                        </el-form-item>
                      </el-form>
                    </template>
                  </template>
                </el-table-column>
                <!-- 展开行 end -->

                <el-table-column
                  v-for="(value, key) in robotTableFields"
                  :key="key"
                  :prop="value"
                  :label="key"
                  min-width="130"
                >
                  <template slot-scope="scope">
                    <template>
                      <template v-if="value == 'applicableModel'">
                        <!-- 机器人型号 start -->
                        <el-input
                          class="input-name"
                          v-model="scope.row[value]"
                          placeholder="机器人型号"
                        ></el-input>
                        <!-- 机器人型号 end -->
                      </template>

                      <template v-if="value == 'robotCount'">
                        <!-- 机器人数量 start -->
                        <el-input
                          ref="robotTableInputRef"
                          class="input-name"
                          v-model="scope.row[value]"
                          placeholder="机器人数量"
                        ></el-input>
                        <!-- 机器人数量 end -->
                      </template>
                    </template>
                  </template>
                </el-table-column>

                <el-table-column label="操作" min-width="130">
                  <template slot-scope="scope">
                    <el-tooltip
                      class="add-item"
                      effect="dark"
                      content="删除"
                      placement="top"
                    >
                      <el-button
                        class="icon-size"
                        @click.native.prevent="
                          delectOperatorRow(scope.$index, scope.row)
                        "
                        type="text"
                        icon="el-icon-remove-outline"
                      >
                      </el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
              </el-table>
              <!-- 表格 end -->
            </el-card>
          </el-form-item>
          <!-- 机器人列表 end -->

          <!-- 工厂名称 start -->
          <el-form-item label="工厂名称" prop="factoryId">
            <!-- <el-input type="text" v-model="validateForm.factoryName" autocomplete="off" placeholder="请输入工厂名称"></el-input> -->
            <el-select
              v-model="validateForm.factoryId"
              filterable
              placeholder="请选择工厂名称"
              @change="onChangeFactory"
              style="width: 100%;"
            >
              <el-option
                v-for="(item, index) in factoryList"
                :key="index"
                :label="item.scenicSpotFname"
                :value="item.scenicSpotFid"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 工厂名称 end -->

          <!-- 创建时间 start -->
          <!-- <el-form-item label="创建时间" prop="createTime">
            <el-input type="text" v-model="validateForm.createTime" autocomplete="off" placeholder="请输入创建时间"></el-input>
          </el-form-item> -->
          <!-- 创建时间 end -->

          <!-- 生产完毕 start -->
          <el-form-item label="生产完毕" prop="type">
            <el-select
              v-model="validateForm.type"
              filterable
              placeholder="请选择生产完毕"
              style="width: 100%;"
            >
              <el-option
                v-for="(item, index) in typeList"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 生产完毕 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input
              type="text"
              v-model="validateForm.notes"
              autocomplete="off"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: 'CommonFault',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      // input: '', // 请输入公司名称
      searchCompanyName: '', // 搜索的公司名称
      tableFields: {
        //导出Excel表格的表头设置
        生产批次: 'name',
        工厂质检项: 'inspectionName',
        景区验收项: 'appectance',
        机器型号: 'applicableModel',
        机器数量: 'robotCount',
        工厂名称: 'factoryName',
        创建时间: 'createTime',
        生产完毕: 'type',
        备注: 'notes'
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      typeList: [
        {
          value: 1,
          label: '否'
        },
        {
          value: 2,
          label: '是'
        }
      ], // 生产完毕选项

      searchValidateForm: {
        factory: '', // 工厂名称
        name: '' // 生产批次
      }, // 搜索表单验证对象
      searchVF: {
        factory: '', // 工厂名称
        name: '' // 生产批次
      }, // 搜索需要提交的表单
      validateForm: {},
      total: 0,

      controlList: [], // 质检项列表
      checkList: [], // 质检项列表
      searchFactoryList: [], // 搜索时的工厂列表
      factoryList: [], // 提交时的工厂列表
      dialogTitle: '', // dialog的标题

      tableLoading: false, // 提交表单中table的loading状态
      // 运营人员成本表格字段
      robotTableFields: {
        机器人型号: 'applicableModel',
        机器人数量: 'robotCount'
      },

      // 提交表单验证规则
      validateFormRules: {
        // 生产批次
        name: [
          {
            validator: checkRequireMethod('生产批次'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 质检项
        inspectionId: [
          {
            validator: checkRequireMethod('质检项'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 验收项
        appectanceId: [
          {
            validator: checkRequireMethod('验收项'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 机器型号
        applicableModel: [
          {
            validator: checkRequireMethod('机器型号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 工厂名称
        factoryId: [
          {
            validator: checkRequireMethod('工厂名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 生产完毕
        type: [
          {
            validator: checkRequireMethod('生产完毕'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        robotTable: [
          {
            validator: this.robotCheckRequireMethod('表格'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 搜索表单验证规则
      searchValidateFormRules: {}
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }    

    this.initTableData()
    this.getValidateForm()
    this.getFactoryList()
    this.getControlList()
    this.getCheckList()
  },
  methods: {
    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    robotCheckRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        // console.log(this.resolventList);

        // 表格是否为空(true不为空，false为空)
        var tag = false

        // 遍历表格列表，如果其中某项的title没有值时，则校验不能通过
        // console.log(this.tableData);
        if (!this.validateForm.robotList) {
          this.validateForm.robotList = []
        }
        this.validateForm.robotList.forEach((item) => {
          // console.log(item);
          if (name == '表格' && item) {
            if (!item.applicableModel) {
              tag = false // 表示有空值
              return callback(new Error('机器人型号不能为空'))
            } else if (!item.robotCount) {
              tag = false // 表示有空值
              return callback(new Error('机器人数量不能为空'))
            } else {
              if (isNaN(item.robotCount) || item.robotCount < 0) {
                return callback(new Error('机器人数量必须为正整数'))
              } else {
                tag = true
              }
            }
          }
        })
        // console.log(tag);
        if (name == '表格' && tag) {
          value = '这里随便给一个值就行' // 随便给一个值使验证通过
        }
        // console.log(value);
        // console.log(name);

        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    },

    // 获取搜索表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        factoryId: ''
      }
      // console.log(this.validateForm);
    },

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = {
        ...validateForm,
        inspectionId: '',
        robotList: [],
        factoryId: ''
      }
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var factory = this.searchVF.factory
      if (factory == '' || !factory) {
        factory = null
      }

      var name = this.searchVF.name
      if (name == '' || !name) {
        name = null
      }

      this.loadingTable = true
      const { data: res } = await this.$http.get(
        '/system/production_info/list',
        {
          params: {
            factory: factory, // 工厂名称
            name: name, // 生产批次
            pageNum: (this.currentPage - 1) * this.pageSize, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res.data);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total
        // this.$message({
        //   message: res.msg,
        //   type: 'success',
        //   offset: 100,
        //   center: true,
        //   duration: 1000,
        // })
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 获取工厂质检项列表
     */
    async getControlList() {
      const { data: res } = await this.$http.get('/system/srqis/getAll', {
        params: {
          type: 1
        }
      })
      if (res.state == '200') {
        this.controlList = res.data
      }
    },

    /**
     * 获取工厂质检项列表
     */
    async getCheckList() {
      const { data: res } = await this.$http.get('/system/srqis/getAll', {
        params: {
          type: 2
        }
      })
      if (res.state == '200') {
        this.checkList = res.data
      }
    },

    /**
     * 获取工厂列表（发货管理下的“工厂列表”接口）
     */
    async getFactoryList() {
      const { data: res } = await this.$http.get(
        '/system/production_info/spotFactoryList'
      )
      console.log(res.data.list)
      this.searchFactoryList = res.data.list
      this.factoryList = res.data.list
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/production_info/add',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: '添加成功',
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/production_info/edit',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: '编辑成功',
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 删除表格数据
     */
    async deleteTableData(id) {
      const { data: res } = await this.$http.delete(
        '/system/production_info/delete',
        {
          params: {
            id: id
          }
        }
      )
      // console.log(res);
      if (res.state == '200') {
        this.$message({
          type: 'success',
          message: '删除成功',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          type: 'error',
          message: res.msg,
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (
        this.searchValidateForm.factory == this.searchVF.factory &&
        this.searchValidateForm.name == this.searchVF.name
      ) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.factory = this.searchValidateForm.factory
      this.searchVF.name = this.searchValidateForm.name

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.factory = ''
      this.searchValidateForm.name = ''

      // 重置当前页数
      this.currentPage = 1
      // 重置每页条数
      this.pageSize = 10

      // 重新获取表格数据
      this.getTableData()
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增生产管理'
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.appectance = parseFloat(rowObj.appectance)
      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑生产管理'
    },

    // 点击删除时触发
    deleteRow(index, row) {
      // console.log(index, row);
      this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.deleteTableData(row.id)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 提交时选项变化触发 start
    /**
     * 质检项变化时触发
     */
    onChangeControl(inspectionId) {
      this.controlList.forEach((item) => {
        if (item.id == inspectionId) {
          this.validateForm.inspectionName = item.name
        }
      })
    },
    /**
     * 工厂名称项变化时触发
     */
    onChangeFactory(factoryId) {
      this.controlList.forEach((item) => {
        if (item.spotId == factoryId) {
          this.validateForm.factoryName = item.spotName
        }
      })
    },
    // 提交时选项变化触发 end

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 点击删除机器人表格行时触发
    delectOperatorRow(index, row) {
      this.validateForm.robotList.splice(index, 1)
      // 获取焦点再失去焦点，实现使校验提示消失的效果
      // this.$refs.tableInputRef[0]表示找到第一个input输入框
      // console.log(this.$refs.tableInputRef)
      this.$refs.robotTableInputRef[0].focus()
      this.$refs.robotTableInputRef[0].blur()
    },

    // 点击添加一行机器人信息
    onAddOperatorTable() {
      this.validateForm.robotList.push({
        applicableModel: '',
        robotCount: ''
      })
    },

    // 验收项变化触发
    onChangeAppectance(val) {
      // console.log(val);
      this.checkList.forEach((item) => {
        if(item.id == val) {
          this.validateForm.appectance = item.name
        }
      })
    }
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '否'
          } else if (row[value] == 2) {
            result = '是'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

